<template>
    <div class="luck-draw-page">
        <!-- 头 -->
        <van-nav-bar title="" left-arrow :border="false" @click-left="goBack"/>
        <!-- 玩法规则 -->
		<img class="go-rule" src="../assets/imgs/go-rule-draw.png" alt="" @click="goRulePage">
        <div class="space" @click="freePasswordChange"></div>
         <!-- 我的蚪金 -->
        <div class="money">
            <img src="../assets/imgs/draw-my-money.png" alt="">
            <div class="money-text">我的蚪金：{{user_info.dw_money}}</div>
            <div class="money-price">0.5蚪金/次</div>
        </div>
        <!-- 抽奖 -->
        <div class="wheel-main">
            <div class="wheel-pointer-box">
                    <div class="wheel-pointer" @click="rotate_handle()" :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}">
                        <img v-if="free_num == 0" src="../assets/imgs/draw_btn.png" alt="">
                        <img v-if="free_num > 0" src="../assets/imgs/draw_btn_free.png" alt="">
                    </div>
            </div>               
            <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">                   
                <div class="prize-list">
                    <div class="prize-item" v-for="(item,index) in prizeList" :key="index">
                        
                        <div class="prize-name">
                            <span v-if="item.id == 3 || item.id == 4 || item.id == 5 || item.id == 7">
                                {{parseInt(item.prize_money)}}
                            </span>
                            {{item.prize}}
                        </div>
                        

                        <div class="prize-pic">
                            <img :src="item.prize_img">
                        </div>

                    </div>
                </div>
            </div>
        </div>
       
        <!-- 我的战绩 -->
        <div class="my-record">
            <img src="../assets/imgs/my-record.png" alt="" @click="goMore">
        </div>
        <!-- 中奖用户 -->
        <div class="win-box">
            <div class="win-box-bg">
                <img src="../assets/imgs/draw-result-bg.png" alt="">
            </div>
            <div class="win-content">
                <!-- 标题 -->
                <!-- <div class="win-title">
                    <div class="win-title-line"></div>
                    <div class="win-title-text">中奖用户</div>
                    <div class="win-title-line"></div>
                </div> -->
                <!-- 列表 -->
                <div class="win-list">
                    <div class="win-list-item win-list-item-title">
                        <div>中奖时间</div>
                        <div>获奖人</div>
                        <div>奖品名称</div>
                    </div>
                    <div class="win-list-item-box">
                        <vueSeamless :data='winList' :class-option="option">
                            <div class="win-list-item" v-for="(item,index) in winList" :key="index">
                                <div>{{item.add_time | timeFormat}}</div>
                                <div>{{item.user_name | userNameFormat}}</div>
                                <div>{{item.prize}}</div>
                            </div>   
                        </vueSeamless>
                         
                    </div>
                </div>
                
            </div>
        </div>


        <!-- 抽奖提示 -->
		<van-popup v-model="showDrawNoticePopup">
			<div class="guess-box">
				<img src="../assets/imgs/draw-notice.png" alt="">
				<div class="guess-content">
					<div class="guess-content-text">您正在进行幸运转盘抽奖需消耗0.5蚪金，是否确认操作？</div>
                    <div class="tip-box">
                        <van-checkbox v-model="noTip">小额免密支付</van-checkbox>
                    </div>
					<div class="guess-btn-box">
						<img src="../assets/imgs/draw-notice-cancal.png" alt="" @click="closeDrawNotice">
						<img src="../assets/imgs/draw-notice-confirm.png" alt="" @click="showPayPassword">
					</div>
					
				</div>
			</div>
		</van-popup>


        <!-- 抽奖结果 -->
		<!-- <van-popup v-model="showDrawResultPopup">
			<div class="draw-result">
                <div class="draw-result-bg">
                    <img src="../assets/imgs/draw-result-img.png" alt="">
                </div>
				<div class="draw-result-box">
                    <div class="draw-result-title">
                        <div class="draw-result-title-line"></div>
                        <div class="draw-result-title-text">恭喜您中奖啦~</div>
                        <div class="draw-result-title-line"></div>
                    </div>
                    <div class="draw-result-img">
                        <img :src="draw_result.prize_img" alt="">
                    </div>
					<div class="draw-result-text">
                        <div v-if="draw_result.id == 3 || draw_result.id == 4 || draw_result.id == 5 || draw_result.id == 7">恭喜获得{{draw_result.prize}}{{draw_result.prize_money}}</div>
                        <div v-if="draw_result.id == 6">恭喜您获得免费抽奖一次</div>
                        <div v-if="draw_result.id == 1 || draw_result.id == 2">恭喜您获得{{draw_result.prize}}一台</div>
                    </div>
                    <div class="draw-result-btn" @click="rotate_handle">再抽一次</div>
					<div class="draw-result-tip">
                        <div v-if="draw_result.id == 3 || draw_result.id == 4 || draw_result.id == 5 || draw_result.id == 7">温馨提示：蚪金红包领取后直接发送到您的蚪金账户中，注意查收；若有延迟，请耐心等待~</div>
                        <div v-if="draw_result.id == 1 || draw_result.id == 2">温馨提示：请前往我的战绩处填写领取奖品信息，稍后会有客服人员与您核对，请耐心等待~</div>
                    </div>
                    
					
				</div>
			</div>
		</van-popup> -->
        <div class="popup" v-if="showDrawResultPopup">
			<!-- 阴影 -->
			<div class="popup-shadow" @click="closeDrawResult"></div>
			
			<div class="draw-result">
                <div class="draw-result-bg">
                    <img src="../assets/imgs/draw-result-img.png" alt="">
                </div>
				<div class="draw-result-box">
                    <!-- <div class="draw-result-title">
                        <div class="draw-result-title-line"></div>
                        <div class="draw-result-title-text">恭喜您中奖啦~</div>
                        <div class="draw-result-title-line"></div>
                    </div> -->
                   
					<div class="draw-result-text">
                        <div v-if="draw_result.id == 3 || draw_result.id == 4 || draw_result.id == 5 || draw_result.id == 7">恭喜获得{{parseInt(draw_result.prize_money)}}{{draw_result.prize}}</div>
                        <div v-if="draw_result.id == 6">恭喜您获得免费抽奖一次</div>
                        <div v-if="draw_result.id == 1 || draw_result.id == 2">恭喜您获得{{draw_result.prize}}一台</div>
                    </div>
                     <div class="draw-result-img">
                        <img :src="draw_result.prize_img" alt="">
                    </div>
                    <div class="draw-result-btn">
                        <img v-if="draw_result.id == 6" src="../assets/imgs/draw-result-again.png" alt="" @click="rotate_handle">
                        <img v-if="draw_result.id == 1 || draw_result.id == 2 || draw_result.id == 3 || draw_result.id == 4 || draw_result.id == 5 || draw_result.id == 7" src="../assets/imgs/draw-result-ok.png" alt="" @click="closeDrawResult">
                    </div>
					<div class="draw-result-tip">
                        <div v-if="draw_result.id == 3 || draw_result.id == 4 || draw_result.id == 5 || draw_result.id == 7">温馨提示：蚪金红包直接发送到您的蚪金账户中，注意查收；若有延迟，请耐心等待</div>
                        <div v-if="draw_result.id == 1 || draw_result.id == 2">温馨提示：请前往我的战绩处填写领取奖品信息，稍后会有客服人员与您核对，请耐心等待~</div>
                    </div>
                    
					
				</div>
			</div>
		</div>

		<!-- 验证支付密码 -->
		<div class="popup" v-if="showPayPasswordPopup">
			<!-- 阴影 -->
			<div class="popup-shadow"></div>
			
			<div class="popup-password-input">
				<div class="popup-password-input-title">
					<van-icon name="cross" @click="closePayPassword"/>
					<span>输入支付密码，以验证身份</span>
				</div>
				<!-- 密码框 -->
				<van-password-input :value="payPw"/>
				<!-- 数字键盘 -->
				<van-number-keyboard :show="true" :hide-on-click-outside="false" @input="onInput" @delete="onDelete" />
			</div>
		</div>


    </div>
</template>



<script>

import { mapState } from 'vuex'
import { mapMutations } from 'vuex'

import vueSeamless from 'vue-seamless-scroll'

export default {
    data(){
        return{
            winList:[],//中奖列表
            prizeList:[],//奖品列表
            free_num:0,
            start_rotating_degree: 0, //初始旋转角度
            rotate_angle: 0, //将要旋转的角度
            start_rotating_degree_pointer: 0, //指针初始旋转角度
            rotate_angle_pointer: 0, //指针将要旋转的度数
            rotate_transition: "transform 6s ease-in-out", //初始化选中的过度属性控制
            rotate_transition_pointer: "transform 12s ease-in-out", //初始化指针过度属性控制
            click_flag: true, //是否可以旋转抽奖
            index: 0 ,

            showDrawNoticePopup:false,//抽奖提示框
            noTip:false,//提示 开启小额免密支付
            showPayPasswordPopup:false,
            payPw:'',
            showDrawResultPopup:false,//抽奖结果框
            draw_result:{//抽奖结果
                id:0,
                prize:'',
                prize_img:'',
                prize_money:''
            },
            
            //中奖列表滚动参数
            option: {
                step: 1,
                limitMoveNum: 9,
                hoverStop:false,
                openTouch: false,
                waitTime: 10,
                direction: 1,
                singleHeight: 30,
            },


        }
    },
    components: {
        vueSeamless
    },
    computed:{
        ...mapState(['user_info']),
    },
    filters: {
        //格式化中奖时间
        timeFormat(val) {
            
            let timestamp = parseInt(val) * 1000;
            let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
            let year = date.getFullYear();
            let month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
            let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
            let hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
            let minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
            let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
            return hour + ":" + minute + ":" + seconds;
            // return year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + seconds;
        },
        //格式化中奖用户名
        userNameFormat(val){
            let user_name = val + '';
            if(user_name.length >= 3){
                let start = user_name.substring(0,1);
                let end = user_name.substring(user_name.length - 1,user_name.length);
                return start + '**' + end;
            }else{

            }
        }
    },
    created(){
        this.utils.info('user_info...',this.user_info);
        if(this.$route.params.id){
			this.utils.info('有id...已登录...');
			this.user_info.user_id = this.$route.params.id;
		}else{
			this.utils.info('没有id...未登录...');
			this.user_info.user_id = 1;
        }
        this.utils.info('user_info...',this.user_info);

        //获取用户信息
		this.getUserInfo('1');
    },
    mounted(){
        
    },
    methods:{
        ...mapMutations(['changeUserInfo']),

        //返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
        },
        //玩法规则
		goRulePage(){
			this.$router.push({
				name:'luckDrawRule'
			})
		},
        //我的战绩
		goMore(){
			this.utils.info('战绩更多');
			this.$bridge.callhandler('goMore', {tag:"战绩更多"}, (data) => {})
        },
        //获取用户信息 type 1 去请求奖品列表和用户列表 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
                    this.user_info.token = data.token;
                    this.user_info.free_password = data.free_password;

                    if(type == '1'){
                        //获取奖品列表
                        this.getPrizeList();

                        //获取中奖用户列表
                        this.getWinList();
                    }
                    
                }
			})
        },
        //获取奖品列表
        getPrizeList(){
            let option = {
                token: this.user_info.token
            }
            this.http.post(this.http._HOST + 'turntable/prize',option).then(result =>{
                this.utils.info('获取奖品列表...',result); 
                if(result.success){
                    this.prizeList = result.data.prize_list;
                    this.free_num = result.data.free_num;
                }
            })
        },
        //获取中奖用户列表
        getWinList(){
            let option = {
                token: this.user_info.token
            }
            this.http.post(this.http._HOST + 'turntable/record',option).then(result =>{
                this.utils.info('获取中奖用户列表...',result); 
                if(result.success){
                    this.winList = result.data.reverse();

                }
            })
        },
        //免密码支付
        freePasswordChange(){
            this.utils.info('noTip...',this.noTip);
            let is_free = 0;
            if(this.noTip){
                is_free = 1;
            }else{
                is_free = 2;
            }
            let option = {
                token: this.user_info.token,
                is_free: is_free
            }
            this.http.post(this.http._HOST + 'Turntable/set_free',option).then(result =>{
                this.utils.info('免密码支付...',result); 
                if(result.success){
                    this.noTip = false;
                    this.utils.info('设置成功');

                    if(is_free == 1){
                        this.user_info.free_password = 1;
                    }else{
                        this.user_info.free_password = 0;
                    }
                }
            })

        },
        //抽奖
        rotate_handle() {
            if(this.showDrawResultPopup){
                this.showDrawResultPopup = false;
            }
            
            if(this.user_info.pay_password == ''){
                this.utils.showAlert('请先设置支付密码');
                return false;
            }
            if(parseFloat(this.user_info.dw_money) < 0.5 ){
                this.utils.showAlert('您的蚪金金额不足');
                return false;
            }
            if(this.user_info.free_password != 0 || this.free_num != 0){
                // this.showPayPassword();
                //this.showDrawNotice(); 
                //请求抽奖概率
                this.getDrawResult();
                
            }else{
                this.showDrawNotice(); 
            }
            
        },
        //转起来
        rotating() {
            if (!this.click_flag) return;
            var type = 0; // 默认为 0  转盘转动 1 箭头和转盘都转动(暂且遗留)
            var during_time = 5; // 默认为1s
            var random = Math.floor(Math.random() * 7);
            var result_index = this.index ; // 最终要旋转到哪一块，对应prize_list的下标
            var result_angle = [337.5, 292.5, 247.5, 202.5, 157.5, 112.5, 67.5, 22.5]; //最终会旋转到下标的位置所需要的度数
            var rand_circle = 6; // 附加多转几圈，2-3
            this.click_flag = false; // 旋转结束前，不允许再次触发
            if (type == 0) {
                // 转动盘子
                var rotate_angle =
                this.start_rotating_degree +
                rand_circle * 360 +
                result_angle[result_index] -
                this.start_rotating_degree % 360;
                this.start_rotating_degree = rotate_angle;
                this.rotate_angle = "rotate(" + rotate_angle + "deg)";
                // // //转动指针
                // this.rotate_angle_pointer = "rotate("+this.start_rotating_degree_pointer + 360*rand_circle+"deg)";
                // this.start_rotating_degree_pointer =360*rand_circle;
                var that = this;
                // 旋转结束后，允许再次触发
                setTimeout(function() {
                    that.click_flag = true;
                    that.game_over();
                }, during_time * 1000 + 1500); // 延时，保证转盘转完
            } else {
                //
            }
        },
        game_over() {

            if(this.draw_result.id == 6){
                this.free_num = 1;
            }

            if(this.draw_result.id != 8){
                //刷新用户钱包
                this.getUserInfo('2');

                this.showDrawResultPopup = true;
            }
            

            
        },
        //显示抽奖提示
        showDrawNotice(){
            this.showDrawNoticePopup = true;
        },
        //关闭抽奖提示
        closeDrawNotice(){
            this.showDrawNoticePopup = false;
        },
        //显示验证密码
		showPayPassword(){
            this.showDrawNoticePopup = false;
			this.showPayPasswordPopup = true;
		},
		//关闭验证密码
		closePayPassword() {
			this.showPayPasswordPopup = false;
			this.payPw = '';

        },
        //关闭抽奖结果
        closeDrawResult(){
            this.showDrawResultPopup = false;
        },
		// 数字键盘 事件
		onInput(key) {
			
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
                
                let pay_password = this.payPw;
                this.closePayPassword();

                //请求抽奖概率
                this.getDrawResult(pay_password);
                
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
        },
        //请求抽奖概率
        getDrawResult(pay_password){
            // this.utils.showLoading();
            var pay_password = pay_password || '';
            let option = {
                pay_password: pay_password,
                token: this.user_info.token
            }
            this.http.post(this.http._HOST + 'turntable/draw',option).then(result =>{
                this.utils.info('抽奖...',result); 
                // this.utils.dismissLoading(); 
                if(result.success){
                    
                    if( this.free_num == 0){
                        this.user_info.dw_money = (parseFloat(this.user_info.dw_money) -0.5).toFixed(2);
                    }{
                        this.free_num = 0;
                    }
                    
                    this.draw_result = result.data;
                    this.index = parseInt(result.data.id) -1;//指定每次旋转到的奖品下标
                    this.rotating();

                    if(this.noTip){
                        //设置免密码支付
                        this.freePasswordChange();
                    }

                }else{
                    this.utils.showAlert(result.message);
                }
            })
        }
    }
    
}
</script>



<style lang="scss" scoped>
    .luck-draw-page{
		position: relative;
		background: url(../assets/imgs/luck-draw-bg.png) no-repeat center top;
		background-repeat: no-repeat;
		// background-size: cover;
        background-size: 100%;
	}
	.van-nav-bar{
		background: transparent;
	}
	.van-nav-bar .van-icon{
		font-size:20px;
		color:#fff;
	}
    
    /* 玩法规则 */
	.go-rule{
		position: absolute;
		width:85px;
		right:0;
		top:100px;
	}

    /* 占位 */
    .space{
        padding-bottom:170px;
    }

    /* 圆盘抽奖 */
    .wheel-main {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }
    .wheel-bg {
        width: 300px;
        height: 300px;
        background: url("../assets/imgs/draw_wheel.png") no-repeat center top;
        background-size: 100%;
        color: #fff;
        font-weight: 500;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        transition: transform 3s ease;
    }
    .wheel-pointer-box {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 5;
        transform: translate(-50%, -60%);
        // width: 80px;
        // height: 80px;
    }
    .wheel-pointer {
        // width: 100%;
        // height: 100%;
        // background: url("../assets/imgs/draw_btn.png") no-repeat center top;
        // background-size: 100%;
        // transform-origin: center 60%;
    }
    .wheel-pointer img{
        display: block;
        width:80px;
        // width: 100%;
        // height: 100%;
        // background: url("../assets/imgs/draw_btn.png") no-repeat center top;
        // background-size: 100%;
        // transform-origin: center 60%;
    }
    .wheel-bg div {
        text-align: center;
    }
    .prize-list {
        width: 100%;
        height: 100%;
        position: relative;
    }
    .prize-list .prize-item {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        width: 94px;
        height: 144px;
        padding-top:40px;
    }
    .prize-list .prize-item:first-child {
        top: 0;
        left: 133px;
        transform: rotate(20deg);
        color:#FEE400;
    }
    .prize-list .prize-item:nth-child(2) {
        top: 50px;
        left: 172px;
        transform: rotate(67deg);
        color:#FF5B1A;
    }
    .prize-list .prize-item:nth-child(3) {
        top: 105px;
        left: 170px;
        transform: rotate(-250deg);
        color:#FEE400;
    }
    .prize-list .prize-item:nth-child(4) {
        top: 144px;
        left: 131px;
        transform: rotate(-210deg);
        color:#FF5B1A;
    }
    .prize-list .prize-item:nth-child(5) {
        top: 147px;
        left: 70px;
        transform: rotate(-160deg);
        color:#FEE400;
    }
    .prize-list .prize-item:nth-child(6) {
        top: 102px;
        left: 30px;
        transform: rotate(-111deg);
        color:#FF5B1A;
    }
    .prize-list .prize-item:nth-child(7) {
        top: 44.8px;
        left: 29px;
        transform: rotate(-69deg);
        color:#FEE400;
    }
